/******************
LINKS & BUTTONS
******************/
a {
  transition: all 0.25s ease-out;
}

button {
  background-color: transparent;
  border: none;
}

.close-button {
  height: 24px;
  width: 24px;
}
.close-button svg {
  fill: #fffef8;
  transition: all 0.25s ease-out;
}
.close-button:hover svg {
  fill: #dba349;
}

.link-eqlipse, .link-eqlipse--dark {
  position: relative;
}
.link-eqlipse::before, .link-eqlipse--dark::before, .link-eqlipse::after, .link-eqlipse--dark::after {
  content: "";
  border-radius: 50%;
  width: 30px;
  height: 30px;
  position: absolute;
  right: -45px;
  transition: all ease-in-out 0.6s, background-color ease-in-out 0.15s;
  background-color: transparent;
}
.link-eqlipse::before, .link-eqlipse--dark::before {
  top: 0;
}
.link-eqlipse::after, .link-eqlipse--dark::after {
  background-image: url("./../assets/images/buttons/chevron_navy.svg");
}
.link-eqlipse:hover, .link-eqlipse--dark:hover {
  color: #dba349;
}
.link-eqlipse:hover::before, .link-eqlipse--dark:hover::before {
  background-color: #dba349;
  top: -2px;
  right: -48px;
  height: 33px;
  width: 34px;
}
.link-eqlipse:hover::after, .link-eqlipse--dark:hover::after {
  background-color: #002836;
  background-image: url("./../assets/images/buttons/chevron_cream.svg");
  right: -48px;
  width: 29px;
  height: 29px;
}
.link-eqlipse--dark {
  color: #e6eaeb;
}
.link-eqlipse--dark::before {
  top: -4px;
}
.link-eqlipse--dark::after {
  background-image: url("./../assets/images/buttons/chevron_navy_50.svg");
}
.link-eqlipse--dark:hover::before {
  top: -6px;
  width: 33px;
  right: -47px;
}

.dark .link-eqlipse, .dark .link-eqlipse--dark {
  color: #e6eaeb;
}
.dark .link-eqlipse::after, .dark .link-eqlipse--dark::after {
  background-image: url("./../assets/images/buttons/chevron_navy_50.svg");
}
.dark .link-eqlipse:hover, .dark .link-eqlipse--dark:hover {
  color: #dba349;
}

footer .link-eqlipse, footer .link-eqlipse--dark {
  color: #96a7ad;
  width: -moz-fit-content;
  width: fit-content;
}
footer .link-eqlipse::after, footer .link-eqlipse--dark::after {
  background-image: url("./../assets/images/buttons/chevron_navy_75.svg");
}
footer .link-eqlipse:hover::before, footer .link-eqlipse--dark:hover::before {
  width: 33px;
  right: -47px;
}

